* {
  padding: 15px;
}

.search-form {
  border-bottom: 2px solid #000;
  justify-content: center;
  column-gap: 15px;
  margin-top: 50px;
  padding-bottom: 15px;
  display: flex;
}

.container {
  width: 350px;
  height: 50px;
  background-color: #1e272e;
  border-radius: 4rem;
  align-items: center;
  padding: 10px;
  display: flex;
}

.search__box {
  float: left;
  width: 0;
  height: 4rem;
  color: #f7f7f7;
  opacity: 1;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 2rem;
  outline: none;
  font-size: 1.5rem;
  transition: all .75s ease-in;
  position: relative;
}

.container:hover > .search__box {
  width: 85%;
  padding: 0 1rem;
}

.btn-search {
  color: orange;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid orange;
  border-radius: 15px;
  padding: 5px 8px;
}

.btn-search:hover, .btn-search:focus {
  color: #000;
  background-color: orange;
}

.gallery {
  flex-flow: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
  display: flex;
}

.photo-card {
  border: 1px solid #000;
  flex-direction: column;
  margin: 0;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .photo-card {
    width: calc(50% - 35px);
  }
}

.imag-card {
  max-height: 300px;
  border: 1px solid #000;
  padding: 0;
}

.load-more {
  color: #000;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #000;
  border-radius: 15px;
  margin: auto;
  padding: 5px 8px;
}

.load-more:hover, .load-more:focus {
  color: #fff;
  background-color: #000;
  transition: color .5s ease-in-out, background-color .25s ease-in-out;
}

.info-item {
  color: orange;
  font-family: Lobster;
}

